import { object, string, func, optional, any, define } from 'superstruct';

const NullableObject = define('NullableObject', (value) => {
  return value === null || typeof value === 'object';
});

const Params = object({
  path: string(),
  method: string(),
  headers: object(),
  routeParams: optional(NullableObject),
  queryParams: optional(NullableObject),
  payload: optional(any()),
});

const HttpListener = object({
  onFunction: optional(func()),
  callback: optional(func()),
});

export const CaptureApmMiddlewareConfig = object({
  params: Params,
  callback: func(),
  httpListener: optional(HttpListener),
});

export const StartTransactionSpanConfig = object({
  spanName: string(),
  spanType: string(),
  payload: optional(object()),
});