/**
 * BaseLogger
 * @namespace Core
 */

/**
 * BaseLogger
 * Abstract base class for loggers.
 * @abstract
 * @class
 */
export class BaseLogger {
  /**
   * Writes a log entry.
   * @abstract
   * @param {object} logData - The data to be logged.
   */
  writeLog(logData) {
    throw new Error('writeLog method must be implemented by subclasses');
  }
}