import convict from "convict";
import dotenv from "dotenv";
import { apmConfig } from "./src/apm.js";
import { environmentConfig } from "./src/environment.js";

/**
 * @function loadEnvConfigs
 * @description Loads environment variables from `.env` files based on `NODE_ENV`.
 * @returns {{ override: boolean } | { path: string }} Configuration options for dotenv. Returns an object with either `override` or `path` property.
 */
const loadEnvConfigs = () => {
  const environment = process.env.NODE_ENV || 'dev';
  const disableOverride = process.env.NODE_ENV_DISABLE_OVERRIDE;
  const disableSpecificEnvs = process.env.NODE_ENV_DISABLE_SPECIFICS;

  if (disableOverride) {
    return { override: false };
  }

  return disableSpecificEnvs ? { path: '.env' } : { path: `.env.${environment}` };
};

// Load environment variables
dotenv.config(loadEnvConfigs());

/**
 * @constant configSchema
 * @description Consolidated configuration schema combining all atomic configurations.
 */
const configSchema = {
  ...environmentConfig,
  apm: apmConfig,
};

const convictConf = convict(configSchema);

convictConf.validate({ allowed: "strict" });

export { convictConf };
